// 印尼手机号码规则
export const mobileReg = /^(?:0|\+?62)?(8[0-9]{7,11})$/; // 手机号码/^(?:0|62)?(8\d{9,11})$/; +6281807946689
export const passwordReg = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[`~!@#\$%\^&\*\(\)\-\_\+=;:'",.\?<>\\/\[\]\{\}\|])[a-zA-Z\d`~!@#\$%\^&\*\(\)\-\_\+=;:'",.\?<>\\/\[\]\{\}\|]{8,20}$/;
export const number = /^\d+$/;
export const idReg = /^[1-9]\d{15}$/ //身份证号码非0开始的16位数字


